.friendMessage {
    justify-content: flex-end;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin: 30px 20px;

}

.friendImageAndText {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    align-items: flex-end;
}

.friendImageAndText img {
    width: 50px;
    height: 50px;
    border-style: solid;
    border-radius: 25px;
}

.friendText {
    position: relative;
    display: block;
    text-align: right;
    min-width: 30px;
    min-height: 60px;
    font-family: 'Montserrat';
    margin-right: 20px;
    max-height: 500px;
    background: #0066CC;
    border-radius: 10px 10px 0 10px;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
}
pre{
    word-wrap: break-word;
    white-space: pre-wrap;
}

.friendText:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 100%;
    display: block;
    width: 20px;
    height: 20px;
    background: radial-gradient(40px at top right, transparent 50%, #0066CC 0%) no-repeat left bottom;
}

.friendName {
    padding: 10px 10px 0px 10px;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
}

.friendMessageText {
    font-family: 'Montserrat';
    padding: 0 10px 10px 10px;
    font-style: normal;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
}

.friendTime {
    width: 29px;
    height: 12px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    margin-right: 10px;
}
